import React from 'react'
import Card from "./Card";
import data from "../cards.json";
const Cards = () => {
    // console.log(data);
    return (
        <>
            {data.map(({ lang, url, fcolor, scolor }, index) => {
                return <Card key={index} lang={lang} img={url} fcolor={fcolor} scolor={scolor} />
            })}
        </>
    )
}

export default Cards
